import * as React from 'react';
import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';
import './FAQ.css';



const FAQ = (props: any) => {
  return (
    <Container className="faq-container">

      <Accordion>
        <Accordion.Item eventKey="0">
          <Accordion.Header>{props.faqTitle}</Accordion.Header>
          <Accordion.Body>
            {props.children}
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </Container>
  );
}

export default FAQ;